<template>
  <div>
    <!--  -->
      <v-alert outlined>
        <h1 class="text-center">{{ setYear - 1911 }} </h1>
        <VeLine :data="chartData1" :settings="chartSetNormal"></VeLine>
      </v-alert>

      <!--  -->
      <v-alert outlined>
        <h1 class="text-center">{{ setYear - 1911 }} </h1>
        <VeHistogram :data="chartData2" ></VeHistogram>
      </v-alert>

      <!--  -->
      <v-alert outlined>
        <h1 class="text-center">{{ setYear - 1911 }} </h1>
        <VeHistogram :data="chartData3" :settings="chartSet"></VeHistogram>
        <v-simple-table>
        <thead>
          <tr>
            <th class="title font-weight-bold"></th>
            <th class="title font-weight-bold" v-for="month in 12" :key="month">{{month}} </th>
          </tr>
        </thead>

        <tbody>
          <tr>
            <td></td>
            <td v-for="(list,index) in list.monthLists" :key="index">{{ list.c1 ? ((list.c1 / list.b) * 100).toFixed(2) : 0 }} %</td>
          </tr>
          <tr>
            <td></td>
            <td v-for="(list,index) in list.monthLists" :key="index">{{ list.c2 ? ((list.c2 / list.b) * 100).toFixed(2) : 0 }} %</td>
          </tr>
          <tr>
            <td></td>
            <td v-for="(list,index) in list.monthLists" :key="index">{{ list.c3 ? ((list.c3 / list.b) * 100).toFixed(2) : 0 }} %</td>
          </tr>
          <tr>
            <td></td>
            <td v-for="(list,index) in list.monthLists" :key="index">{{ list.c4 ? ((list.c4 / list.b) * 100).toFixed(2) : 0 }} %</td>
          </tr>
          <tr>
            <td></td>
            <td v-for="(list,index) in list.monthLists" :key="index">{{ list.c5 ? ((list.c5 / list.b) * 100).toFixed(2) : 0 }} %</td>
          </tr>
          <tr>
            <td></td>
            <td v-for="(list,index) in list.monthLists" :key="index">{{ list.c6 ? ((list.c6 / list.b) * 100).toFixed(2) : 0 }} %</td>
          </tr>

        </tbody>
      </v-simple-table>
      </v-alert>
  </div>
</template>

<script>
import VeLine from 'v-charts/lib/line.common'
import VeHistogram from 'v-charts/lib/histogram.common'

export default {
  components:{ VeLine, VeHistogram },
  props:['setYear','list'],

  data(){
    return {
      // 
      chartSetNormal:{
        yAxisType : ['percent'],
      },
      chartSet:{
        yAxisType : ['percent'],
        max:[1]
      },
    }
  },

  computed:{
    //1
    chartData1(){
      return {
        columns: ["", ""],
        rows: this.list.monthLists.map((i,index)=>{
          return {
            '' : (index+1) + '',
            '' : i.b ? (i.b / i.a).toFixed(4) : 0,
          }
        })
      }
    },
    //2
    chartData2(){
      return {
        columns: ["", "","24"],
        rows: this.list.monthLists.map((i,index)=>{
          return {
            '' : (index+1) + '',
            '' : i.b,
            '24' : i.f
          }
        })
      }
    },
    //3
    chartData3(){
      return {
        columns: 
        ["", 
        "", 
        "", 
        "" ,
        "",
        "",
        "",
        ],
        rows: this.list.monthLists.map((i,index)=>{
          return {
            '' : (index+1)+'',
            '' : i.c1 ? (i.c1 / i.b) : 0,
            '' :i.c2 ? (i.c2 / i.b) : 0,
            '' : i.c3 ? (i.c3 / i.b) : 0,
            '' : i.c4 ? (i.c4 / i.b) : 0,
            '' : i.c5 ? (i.c5 / i.b) : 0,
            '' : i.c6 ? (i.c6 / i.b) : 0,
          }
        })
      }
    },

  }
}
</script>