<template>
  <div>
    <!--  -->
      <v-alert outlined>
        <h1 class="text-center">{{ setYear - 1911 }} </h1>
        <VeLine :data="chartData1" :settings="chartSetNormal"></VeLine>
      </v-alert>

      <!--  -->
      <v-alert outlined>
        <h1 class="text-center">{{ setYear - 1911 }} </h1>
        <VeHistogram :data="chartData2" :settings="chartSetNormal"></VeHistogram>
        <v-simple-table>
        <thead>
          <tr>
            <th class="title font-weight-bold"></th>
            <th class="title font-weight-bold" v-for="month in 12" :key="month">{{month}} </th>
          </tr>
        </thead>

        <tbody>
          <tr>
            <td>
              
            </td>
            <td v-for="(list,index) in list.monthLists" :key="index">{{ list.a ? ((list.b1 / list.a) * 100).toFixed(3) : 0 }} %</td>
          </tr>
          <tr>
            <td>
              
            </td>
            <td v-for="(list,index) in list.monthLists" :key="index">{{ list.a ? ((list.b2 / list.a) * 100).toFixed(3) : 0 }} %</td>
          </tr>
          <tr>
            <td>
              
            </td>
            <td v-for="(list,index) in list.monthLists" :key="index">{{ list.a ? ((list.b3 / list.a) * 100).toFixed(3) : 0 }} %</td>
          </tr>
          <tr>
            <td></td>
            <td v-for="(list,index) in list.monthLists" :key="index">{{ list.a ? ((list.b4 / list.a) * 100).toFixed(3) : 0 }} %</td>
          </tr>
        </tbody>
      </v-simple-table>
      </v-alert>

      <!--  -->
      <v-alert outlined>
        <h1 class="text-center">{{ setYear - 1911 }} </h1>
        <VeHistogram :data="chartData3" :settings="chartSetNormal"></VeHistogram>

      </v-alert>
      <!--  -->
      <!-- <v-alert outlined>
        <h1 class="text-center">{{ setYear - 1911 }} </h1>
        <VeHistogram :data="chartData4" :settings="chartSet"></VeHistogram>
        <v-simple-table>
        <thead>
          <tr>
            <th class="title font-weight-bold"></th>
            <th class="title font-weight-bold" v-for="month in 12" :key="month">{{month}} </th>
          </tr>
        </thead>

        <tbody>
          <tr>
            <td></td>
            <td v-for="(list,index) in list.monthLists" :key="index">{{ list.e ? ((list.e / list.d) * 100).toFixed(0) : 0 }} %</td>
          </tr>
        </tbody>
      </v-simple-table>
      </v-alert> -->
  </div>
</template>

<script>
import VeLine from 'v-charts/lib/line.common'
import VeHistogram from 'v-charts/lib/histogram.common'

export default {
  components:{ VeLine, VeHistogram },
  props:['setYear','list'],

  data(){
    return {
      // 
      chartSetNormal:{
        yAxisType : ['percent'],
      },
      chartSet:{
        yAxisType : ['percent'],
        max:[1]
      },
    }
  },

  computed:{
    //1
    chartData1(){
      return {
        columns: ["", ""],
        rows: this.list.monthLists.map((i,index)=>{
          return {
            '' : (index+1) + '',
            '' : i.b ? (i.b / i.a).toFixed(4) : 0,
          }
        })
      }
    },
    //2
    chartData2(){
      return {
        columns: ["", "", "", "" ,""],
        rows: this.list.monthLists.map((i,index)=>{
          return {
            '' : (index+1) +'',
            '' : i.a ? (i.b1 / i.a) : 0,
            '' :i.a ? (i.b2 / i.a) : 0,
            '' : i.a ? (i.b3 / i.a) : 0,
            '' : i.a ? (i.b4 / i.a) : 0,
          }
        })
      }
    },
    //3
    chartData3(){
      return {
        columns: ["", ""],
        rows: this.list.monthLists.map((i,index)=>{
          return {
            '' : (index+1) +'',
            '' : i.d ? (i.d / i.c) : 0,
          }
        })
      }
    },
    //4
    // chartData4(){
    //   return {
    //     columns: ["", ""],
    //     rows: this.list.monthLists.map((i,index)=>{
    //       return {
    //         '' : (index+1) +'',
    //         '' : i.e ? (i.e / i.d) : 0,
    //       }
    //     })
    //   }
    // },
  }
}
</script>