<template>
  <div class="FallEventAnalysisSeason">
    <div class="d-flex justify-space-between align-center">
      <v-text-field
        label=""
        :rules="rules"
        maxlength="4"
        counter
        v-model="setYear"
        hide-details
      ></v-text-field>

      <v-spacer></v-spacer>
      <v-select
        :items="seasionItems"
        label=""
        v-model="setSeason"
        return-object
        hide-details
      ></v-select>
    </div>

    <div v-if="dataReady">
      <!-- {{ PDValue }} -->
      <v-simple-table>
        <thead>
          <tr>
            <th class="title font-weight-bold"></th>
            <th class="title font-weight-bold"></th>
            <th class="title font-weight-bold"></th>
            <th class="title font-weight-bold"></th>
            <th class="title font-weight-bold" v-for="month in setSeason.months" :key="month.season">{{month}} </th>
            <th class="title font-weight-bold"></th>
          </tr>
        </thead>

        <tbody>
          <tr>
            <td>--</td>
            <td>(A)</td>
            <td>--</td>
            <td>--</td>
            <td v-for="(list,index) in list.monthLists" :key="index">{{ list.a }}</td>
            <td>--</td>
          </tr>
          <tr>
            <td>1</td>
            <td>(B)</td>
            <td>B/A</td>
            <td>
              <div class="d-flex align-center">
                <v-text-field hide-details v-model="list.bTval" class="ma-0 pa-0"></v-text-field>%
              </div>
            </td>
            <td v-for="(list,index) in list.monthLists" :key="index">{{ list.b ? ((list.b / list.a) * 100).toFixed(2) : 0 }} %</td>
            <td>{{ returnAverage('b','a') }} %</td>
          </tr>
          <tr>
            <td>2.1</td>
            <td>
              (B1)
            </td>
            <td>B1/B</td>
            <td>
              <!-- <div class="d-flex align-center">
                <v-text-field hide-details v-model="list.b1Tval" class="ma-0 pa-0"></v-text-field>%
              </div> -->
            </td>
            <td v-for="(list,index) in list.monthLists" :key="index">{{ list.b ? ((list.b1 / list.b) * 100).toFixed(0) : 0 }} %</td>
            <td>{{ returnAverage('b1','b') }} %</td>
          </tr>
          <tr>
            <td>2.2</td>
            <td>
              (B2)
            </td>
            <td>B2/B</td>
            <td>
              <!-- <div class="d-flex align-center">
                <v-text-field hide-details v-model="list.b2Tval" class="ma-0 pa-0"></v-text-field>%
              </div> -->
            </td>
            <td v-for="(list,index) in list.monthLists" :key="index">{{ list.b ? ((list.b2 / list.b) * 100).toFixed(0) : 0 }} %</td>
            <td>{{ returnAverage('b2','b') }} %</td>
          </tr>
          <tr>
            <td>2.3</td>
            <td>
              (B3)
            </td>
            <td>B3/B</td>
            <td>
              <!-- <div class="d-flex align-center">
                <v-text-field hide-details v-model="list.b3Tval" class="ma-0 pa-0"></v-text-field>%
              </div> -->
            </td>
            <td v-for="(list,index) in list.monthLists" :key="index">{{ list.b ? ((list.b3 / list.b) * 100).toFixed(0) : 0 }} %</td>
            <td>{{ returnAverage('b3','b') }} %</td>
          </tr>
          <tr>
            <td>2.4</td>
            <td>(B4)</td>
            <td>B4/B</td>
            <td>
              <!-- <div class="d-flex align-center">
                <v-text-field hide-details v-model="list.b4Tval" class="ma-0 pa-0"></v-text-field>%
              </div> -->
            </td>
            <td v-for="(list,index) in list.monthLists" :key="index">{{ list.b ? ((list.b4 / list.b) * 100).toFixed(0) : 0 }} %</td>
            <td>{{ returnAverage('b4','b') }} %</td>
          </tr>
          <tr>
            <td>3</td>
            <td>(C)</td>
            <td>C/B</td>
            <td>
              <!-- <div class="d-flex align-center">
                <v-text-field hide-details v-model="list.cTval" class="ma-0 pa-0"></v-text-field>%
              </div> -->
            </td>
            <td v-for="(list,index) in list.monthLists" :key="index">{{ list.b ? ((list.c / list.b) * 100).toFixed(0) : 0 }} %</td>
            <td>{{ returnAverage('c','b') }} %</td>
          </tr>
          <tr>
            <td>4.1</td>
            <td>1(C1)1</td>
            <td>C1/C</td>
            <td>
              <!-- <div class="d-flex align-center">
                <v-text-field hide-details v-model="list.c1Tval" class="ma-0 pa-0"></v-text-field>%
              </div> -->
            </td>
            <td v-for="(list,index) in list.monthLists" :key="index">{{ list.c1 ? ((list.c1 / list.c) * 100).toFixed(0) : 0 }} %</td>
            <td>{{ returnAverage('c1','c') }} %</td>
          </tr>
          <tr>
            <td>4.2</td>
            <td>2(C2)2</td>
            <td>C2/C</td>
            <td>
              <!-- <div class="d-flex align-center">
                <v-text-field hide-details v-model="list.c2Tval" class="ma-0 pa-0"></v-text-field>%
              </div> -->
            </td>
            <td v-for="(list,index) in list.monthLists" :key="index">{{ list.c2 ? ((list.c2 / list.c) * 100).toFixed(0) : 0 }} %</td>
            <td>{{ returnAverage('c2','c') }} %</td>
          </tr>
          <tr>
            <td>4.3</td>
            <td>3(C3)3</td>
            <td>C3/C</td>
            <td>
              <!-- <div class="d-flex align-center">
                <v-text-field hide-details v-model="list.c3Tval" class="ma-0 pa-0"></v-text-field>%
              </div> -->
            </td>
            <td v-for="(list,index) in list.monthLists" :key="index">{{ list.c3 ? ((list.c3 / list.c) * 100).toFixed(0) : 0 }} %</td>
            <td>{{ returnAverage('c3','c') }} %</td>
          </tr>
          <tr>
            <td>--</td>
            <td>(D)</td>
            <td>--</td>
            <td>--</td>
            <td v-for="(list,index) in list.monthLists" :key="index">{{ list.d }}</td>
            <td>--</td>
          </tr>
          <tr>
            <td>5</td>
            <td>>1(E)</td>
            <td>E/D</td>
            <td>
              <!-- <div class="d-flex align-center">
                <v-text-field hide-details v-model="list.eTval" class="ma-0 pa-0"></v-text-field>%
              </div> -->
            </td>
            <td v-for="(list,index) in list.monthLists" :key="index">{{ list.e ? ((list.e / list.d) * 100).toFixed(0) : 0 }} %</td>
            <td>{{ returnAverage('e','d') }} %</td>
          </tr>
        </tbody>
      </v-simple-table>
      <v-textarea label="" auto-grow v-model="list.note"></v-textarea>

      <div class="d-flex justify-space-between">
        <v-btn class="error" @click.stop="delData" v-if="workProcess == 'edit'"
          ></v-btn
        >
        <v-spacer></v-spacer>
        <v-btn
          class="primary"
          @click.stop="saveData"
          v-if="workProcess == 'add'"
          :loading="loading"
          :disabled="loading"
          ></v-btn
        >
        <v-btn
          class="success"
          @click.stop="saveData"
          v-if="workProcess == 'edit'"
          :loading="loading"
          :disabled="loading"
          ></v-btn
        >
      </div>

      <v-divider class="my-2"></v-divider>

      <!--  -->
      <Charts :setYear="setYear" :setSeason="setSeason" :list="list" />
      <!--  end-->
    </div>
  </div>
</template>

<script>
import dayjs from "dayjs";
import PDVFn from "@/assets/js/PDVFn.js";
import * as math from "mathjs"; //mathjs,

import Charts from './seasonChart.vue'

export default {
  components:{ Charts },
  props: ["items"], //items
  data() {
    return {
      useDataBase : 'analysisofevaluationindicators',
      setYear: dayjs().format("YYYY"),
      setSeason: { season: 1, text: "1~3", months: [1, 2, 3] },
      seasionItems: [
        { season: 1, text: "1~3", months: [1, 2, 3] },
        { season: 2, text: "4~6", months: [4, 5, 6] },
        { season: 3, text: "7~9", months: [7, 8, 9] },
        { season: 4, text: "10~12", months: [10, 11, 12] },
      ],
      list: {
        monthLists:[], //
      }, 
      
      workProcess: "add", //
      loading: false,
      rules: [v => v.length <= 4 || 'Max 4 characters'],
      
      // 
      chartSet:{
        yAxisType : ['percent'],
      },
      // chartData: {
      //   columns: ["", ""],
      //   rows: [
      //     { : "1", : 100 },
      //     { : "2", : 0 },
      //     { : "3", : 0 },
      //   ],
      // },
    };
  },

  computed: {
    //
    dataReady() {
      if (
        this.$store.state.userItems.length &&
        this.$store.state.bed_usedItems.length &&
        this.$store.state.behospitalizedItems.length &&
        this.$store.state.analysisofevaluationindicatorsItems
      ) {
        this.checkType(); ///
        return true;
      } else {
        console.log("not yet");
        return false;
      }
    },
    
  },

  methods:{
    //
    returnAverage(key1,key2){
      let total = 0;
      this.list.monthLists.forEach(list=>{
        let val = list[key1] ? math.format(list[key1]/list[key2]*100) : 0
        total = math.add(total,val)  
      })
      return math.format(total / 3,2);
    },
    ///
    checkType() {
      let match = this.$store.state.analysisofevaluationindicatorsItems
        .map((i) => {
          return {
            snkey: i.snkey,
            ...JSON.parse(i.datalist),
          };
        })
        .find((i) => {
          return i.range.setYear == this.setYear && i.range.setSeason.text == this.setSeason.text && i.rangeType == "season" && i.analysisKind == this.analysisKind;;
        });
      if (match) {
        //->
        this.workProcess = "edit";
        this.list = match;
      } else {
        //
        this.workProcess = "add";
        this.newData();
      }
    },
    // 
    newData() {
      this.list = {
        monthLists : []
      }
      this.setSeason.months.forEach(month=>{
        let newMonth = dayjs(`${this.setYear}-${month}`).format("YYYY-MM");
        this.list.monthLists.push(PDVFn.returnMonthDataFallEvent(this.items, newMonth));  //
      })
    },
    ///
    saveData() {
      this.loading = true;
      if (this.workProcess == "add") {
        //
        //range
        this.list.range = {
          setYear : this.setYear,
          setSeason : this.setSeason
        };
        this.list.rangeType = "season";
        this.list.create_man =
          this.$store.state.pData.username +
          "(" +
          dayjs().format("YYYY-MM-DD HH:mm:ss") +
          ")";
        var postData = {
          datalist: JSON.stringify(this.list),
        };
        var url = "general/add/";
        this.workProcess = "edit";
      } else {
        //
        if (!this.list.edit_man) {
          this.list.edit_man = "";
        }
        this.list.edit_man =
          this.$store.state.pData.username +
          "(" +
          dayjs().format("YYYY-MM-DD HH:mm:ss") +
          ")" +
          this.list.edit_man;
        var postData = {
          snkey: this.list.snkey,
          datalist: JSON.stringify(this.list),
          edit_man: "pass",
        };
        var url = "general/edit/";
      }

      this.$api.options(
        url + this.$store.state.databaseName + "/" + this.useDataBase,
        postData
      ).then((rs) => {
        if (rs.state == 1) {
          var pop = { msg: "", type: true, theme: "success" };
          this.$store.commit("snackbar", pop);
          this.loading = false;
          this.$store.dispatch("getItems", "analysisofevaluationindicators");  //
        }
      });
    },
    //
    delData() {
      let delData = this.list;
      this.$confirm("?").then((res) => {
        delData.delman =
          this.$store.state.pData.username +
          "(" +
          dayjs().format("YYYY-MM-DD HH:mm:ss") +
          ")";
        var postData = {
          snkey: delData.snkey,
          tablename: this.useDataBase,
          info: JSON.stringify(delData),
        };

        if (res) {
          this.$api.options(
            "general/delv3/" +
              this.$store.state.databaseName +
              "/" +
              postData.tablename,
            postData
          ).then((rs) => {
            if (rs.state == 1) {
              var pop = { msg: "", type: true, theme: "success" };
              this.$store.commit("snackbar", pop);
              this.newData();
              this.workProcess = "add";
              this.$store.dispatch("getItems", "analysisofevaluationindicators");  //
            }
          });
        }
      });
    },
  },
};
</script>