<template>
  <div class="AnalysisOfEvaluationIndicators">
    <v-card>
      <v-card-title><h2></h2></v-card-title>

      <v-tabs v-model="tab" background-color="transparent" color="warning darken-1" grow>
        <v-tab v-for="item in TabItems" :key="item">
          <h2>{{ item }}</h2>
        </v-tab>
      </v-tabs>
      <v-tabs-items v-model="tab">
        <v-tab-item> <FallEvent class="pa-3" /> </v-tab-item>
        <v-tab-item> <PressureEvent class="pa-3" /> </v-tab-item>
        <v-tab-item> <InfectionEvent class="pa-3" /> </v-tab-item>
        <v-tab-item> <ConstraintEvent class="pa-3" /> </v-tab-item>
        <v-tab-item> <UnplannedEvent class="pa-3" /> </v-tab-item>
      </v-tabs-items>
    </v-card>
    <!-- <pre>
      userItems:{{ $store.state.userItems ? $store.state.userItems.length : ''}}
      bed_usedItems:{{ $store.state.bed_usedItems ? $store.state.bed_usedItems.length : ''}}
      behospitalizedItems:{{ $store.state.behospitalizedItems ? $store.state.behospitalizedItems.length : ''}}
    </pre> -->
    
  </div>
</template>

<script>
import FallEvent from "./FallEvent";
import PressureEvent from "./PressureEvent";
import InfectionEvent from "./InfectionEvent";
import ConstraintEvent from "./ConstraintEvent";
import UnplannedEvent from "./UnplannedEvent";
import dayjs from "dayjs";

export default {
  components:{ FallEvent, PressureEvent, InfectionEvent, ConstraintEvent, UnplannedEvent },
  data() {
    return {
      setMonth: dayjs().format("YYYY-MM"),
      userItems: null, //
      bed_usedItems: null, //
      behospitalizedItems: null, //

      tab: null,
      TabItems: [
        "",
        "",
        "",
        "",
        ""
      ],
    };
  },

  //
  async created() {
    await this.$store.dispatch('getUser'); //
    await this.$store.dispatch('getBedUsed'); //
    await this.$store.dispatch('getBehospitalized'); //
    this.$store.dispatch("getItems", "analysisofevaluationindicators"); //
  },

};
</script>