<template>
  <div>
      <!--  -->
      <v-alert outlined>
        <h1 class="text-center">{{ setYear - 1911 }}{{ setSeason.season }}</h1>
        <VeLine :data="chartData1" :settings="chartSetNormal"></VeLine>
      </v-alert>

      <!--  -->
      <v-alert outlined>
        <h1 class="text-center">{{ setYear - 1911 }}{{ setSeason.season }}</h1>
        <VeHistogram :data="chartData2" :settings="chartSet"></VeHistogram>
        <v-simple-table>
        <thead>
          <tr>
            <th class="title font-weight-bold"></th>
            <th class="title font-weight-bold" v-for="month in setSeason.months" :key="month.season">{{month}} </th>
          </tr>
        </thead>

        <tbody>
          <tr>
            <td>
              
            </td>
            <td v-for="(list,index) in list.monthLists" :key="index">{{ list.a ? ((list.b1 / list.a) * 100).toFixed(3) : 0 }} %</td>
          </tr>
          <tr>
            <td>
              
            </td>
            <td v-for="(list,index) in list.monthLists" :key="index">{{ list.a ? ((list.b2 / list.a) * 100).toFixed(3) : 0 }} %</td>
          </tr>
          <tr>
            <td>
              
            </td>
            <td v-for="(list,index) in list.monthLists" :key="index">{{ list.a ? ((list.b3 / list.a) * 100).toFixed(3) : 0 }} %</td>
          </tr>
          <tr>
            <td></td>
            <td v-for="(list,index) in list.monthLists" :key="index">{{ list.a ? ((list.b4 / list.a) * 100).toFixed(3) : 0 }} %</td>
          </tr>
        </tbody>
      </v-simple-table>
      </v-alert>

      <!--  -->
      <v-alert outlined>
        <h1 class="text-center">{{ setYear - 1911 }}{{ setSeason.season }}</h1>
        <VeHistogram :data="chartData3" :settings="chartSet"></VeHistogram>
        
      </v-alert>
  </div>
</template>

<script>
import VeLine from 'v-charts/lib/line.common'
import VeHistogram from 'v-charts/lib/histogram.common'

export default {
  components:{ VeLine, VeHistogram },
  props:['setYear', 'setSeason' ,'list'],

  data(){
    return {
      // 
      chartSetNormal:{
        yAxisType : ['percent'],
      },
      chartSet:{
        yAxisType : ['percent'],
        digit: 3,
      },
    }
  },

  computed:{
    //1
    chartData1(){
      return {
        columns: ["", ""],
        rows: this.list.monthLists.map((i,index)=>{
          return {
            '' : this.setSeason.months[index]+'',
            '' : i.b ? (i.b / i.a).toFixed(4) : 0,
          }
        })
      }
    },
    //2
    chartData2(){
      return {
        columns: ["", "", "", "" ,""],
        rows: this.list.monthLists.map((i,index)=>{
          return {
            '' : this.setSeason.months[index]+'',
            '' : i.a ? (i.b1 / i.a) : 0,
            '' :i.a ? (i.b2 / i.a) : 0,
            '' : i.a ? (i.b3 / i.a) : 0,
            '' : i.a ? (i.b4 / i.a) : 0,
          }
        })
      }
    },
    //3
    chartData3(){
      return {
        columns: ["", ""],
        rows: this.list.monthLists.map((i,index)=>{
          return {
            '' : this.setSeason.months[index]+'',
            '' : i.d ? (i.d / i.c) : 0,
          }
        })
      }
    },
  }
}
</script>