<template>
  <div class="FallEventAnalysisMonth">
    <div class="d-flex justify-space-between align-center">
      <v-text-field
        type="month"
        label="/"
        v-model="setMonth"
        hide-details
      ></v-text-field>

      <v-spacer></v-spacer>
    </div>
    <div v-if="dataReady">
      <!-- {{ PDValue }} -->
      <v-simple-table>
        <thead>
          <tr>
            <th class="title font-weight-bold"></th>
            <th class="title font-weight-bold"></th>
            <th class="title font-weight-bold"></th>
            <th class="title font-weight-bold"></th>
            <th class="title font-weight-bold">(%)</th>
          </tr>
        </thead>

        <tbody>
          <tr>
            <td>--</td>
            <td>(A)</td>
            <td>{{ list.a }}</td>
            <td>--</td>
            <td>--</td>
          </tr>
          <tr>
            <td>1</td>
            <td>(B)</td>
            <td>{{ list.b }}</td>
            <td>B/A</td>
            <td>{{ list.b ? ((list.b / list.a) * 100).toFixed(2) : 0 }} %</td>
          </tr>
          <tr>
            <td>2.1</td>
            <td>
              (B1)
            </td>
            <td>{{ list.b1 }}</td>
            <td>B1/B</td>
            <td>{{ list.b ? ((list.b1 / list.b) * 100).toFixed(0) : 0 }} %</td>
          </tr>
          <tr>
            <td>2.2</td>
            <td>
              (B2)
            </td>
            <td>{{ list.b2 }}</td>
            <td>B2/B</td>
            <td>{{ list.b ? ((list.b2 / list.b) * 100).toFixed(0) : 0 }} %</td>
          </tr>
          <tr>
            <td>2.3</td>
            <td>
              (B3)
            </td>
            <td>{{ list.b3 }}</td>
            <td>B3/B</td>
            <td>{{ list.b ? ((list.b3 / list.b) * 100).toFixed(0) : 0 }} %</td>
          </tr>
          <tr>
            <td>2.4</td>
            <td>(B4)</td>
            <td>{{ list.b4 }}</td>
            <td>B4/B</td>
            <td>{{ list.b ? ((list.b4 / list.b) * 100).toFixed(0) : 0 }} %</td>
          </tr>
          <tr>
            <td>3</td>
            <td>(C)</td>
            <td>{{ list.c }}</td>
            <td>C/B</td>
            <td>{{ list.b ? ((list.c / list.b) * 100).toFixed(0) : 0 }} %</td>
          </tr>
          <tr>
            <td>4.1</td>
            <td>1(C1)1</td>
            <td>{{ list.c1 }}</td>
            <td>C1/C</td>
            <td>{{ list.c1 ? ((list.c1 / list.c) * 100).toFixed(0) : 0 }} %</td>
          </tr>
          <tr>
            <td>4.2</td>
            <td>2(C2)2</td>
            <td>{{ list.c2 }}</td>
            <td>C2/C</td>
            <td>{{ list.c2 ? ((list.c2 / list.c) * 100).toFixed(0) : 0 }} %</td>
          </tr>
          <tr>
            <td>4.3</td>
            <td>3(C3)3</td>
            <td>{{ list.c3 }}</td>
            <td>C3/C</td>
            <td>{{ list.c3 ? ((list.c3 / list.c) * 100).toFixed(0) : 0 }} %</td>
          </tr>
          <tr>
            <td>--</td>
            <td>(D)</td>
            <td>{{ list.d }}</td>
            <td>--</td>
            <td>--</td>
          </tr>
          <tr>
            <td>5</td>
            <td>>1(E)</td>
            <td>{{ list.e }}</td>
            <td>E/D</td>
            <td>{{ list.e ? ((list.e / list.d) * 100).toFixed(0) : 0 }} %</td>
          </tr>
        </tbody>
      </v-simple-table>
      <v-textarea label="" auto-grow v-model="list.note"></v-textarea>

      <div class="d-flex justify-space-between">
        <v-btn class="error" @click.stop="delData" v-if="workProcess == 'edit'"
          ></v-btn
        >
        <v-spacer></v-spacer>
        <v-btn
          class="primary"
          @click.stop="saveData"
          v-if="workProcess == 'add'"
          :loading="loading"
          :disabled="loading"
          ></v-btn
        >
        <v-btn
          class="success"
          @click.stop="saveData"
          v-if="workProcess == 'edit'"
          :loading="loading"
          :disabled="loading"
          ></v-btn
        >
      </div>
    </div>
  </div>
</template>

<script>
import dayjs from "dayjs";
import PDVFn from "@/assets/js/PDVFn.js";

export default {
  props: ["items"], //items
  data() {
    return {
      useDataBase : 'analysisofevaluationindicators',
      list: {},
      setMonth: dayjs().format("YYYY-MM"),
      workProcess: "add", //
      loading: false,
    };
  },

  computed: {
    //
    dataReady() {
      if (
        this.$store.state.userItems.length &&
        this.$store.state.bed_usedItems.length &&
        this.$store.state.behospitalizedItems.length &&
        this.$store.state.analysisofevaluationindicatorsItems
      ) {
        this.checkType(); ///
        return true;
      } else {
        console.log("not yet");
        return false;
      }
    },
  },

  methods: {
    ///
    checkType() {
      let match = this.$store.state.analysisofevaluationindicatorsItems
        .map((i) => {
          return {
            snkey: i.snkey,
            ...JSON.parse(i.datalist),
          };
        })
        .find((i) => {
          return i.range == this.setMonth && i.rangeType == "month" && i.analysisKind == this.analysisKind;
        });
      if (match) {
        //->
        this.workProcess = "edit";
        this.list = match;
      } else {
        //
        this.workProcess = "add";
        this.newData();
      }
    },
    //   ->  ? ..
    newData() {
      this.list = PDVFn.returnMonthDataFallEvent(this.items, this.setMonth);
    },
    ///
    saveData() {
      this.loading = true;
      if (this.workProcess == "add") {
        //
        this.list.range = this.setMonth;
        this.list.rangeType = "month";
        this.list.create_man =
          this.$store.state.pData.username +
          "(" +
          dayjs().format("YYYY-MM-DD HH:mm:ss") +
          ")";
        var postData = {
          datalist: JSON.stringify(this.list),
        };
        var url = "general/add/";
        this.workProcess = "edit";
      } else {
        //
        if (!this.list.edit_man) {
          this.list.edit_man = "";
        }
        this.list.edit_man =
          this.$store.state.pData.username +
          "(" +
          dayjs().format("YYYY-MM-DD HH:mm:ss") +
          ")" +
          this.list.edit_man;
        var postData = {
          snkey: this.list.snkey,
          datalist: JSON.stringify(this.list),
          edit_man: "pass",
        };
        var url = "general/edit/";
      }

      this.$api.options(
        url + this.$store.state.databaseName + "/" + this.useDataBase,
        postData
      ).then((rs) => {
        console.log(rs);
        if (rs.state == 1) {
          var pop = { msg: "", type: true, theme: "success" };
          this.$store.commit("snackbar", pop);
          this.loading = false;
          this.$store.dispatch("getItems", "analysisofevaluationindicators");  //
        }
      });
    },
    //
    delData() {
      let delData = this.list;
      this.$confirm("?").then((res) => {
        delData.delman =
          this.$store.state.pData.username +
          "(" +
          dayjs().format("YYYY-MM-DD HH:mm:ss") +
          ")";
        var postData = {
          snkey: delData.snkey,
          tablename: this.useDataBase,
          info: JSON.stringify(delData),
        };

        if (res) {
          this.$api.options(
            "general/delv3/" +
              this.$store.state.databaseName +
              "/" +
              postData.tablename,
            postData
          ).then((rs) => {
            if (rs.state == 1) {
              var pop = { msg: "", type: true, theme: "success" };
              this.$store.commit("snackbar", pop);
              this.newData();
              this.workProcess = "add";
              this.$store.dispatch("getItems", "analysisofevaluationindicators");  //
            }
          });
        }
      });
    },
  },
};
</script>

<style scoped>
.v-data-table td {
  font-size: 1rem;
}
</style>