<template>
  <div>
    <!--  -->
      <v-alert outlined>
        <h1 class="text-center">{{ setYear - 1911 }} </h1>
        <VeLine :data="chartData1" :settings="chartSetNormal"></VeLine>
      </v-alert>

      <!--  -->
      <v-alert outlined>
        <h1 class="text-center">{{ setYear - 1911 }} </h1>
        <VeHistogram :data="chartData2" :settings="chartSet"></VeHistogram>
        <v-simple-table>
        <thead>
          <tr>
            <th class="title font-weight-bold"></th>
            <th class="title font-weight-bold" v-for="month in 12" :key="month">{{month}} </th>
          </tr>
        </thead>

        <tbody>
          <tr>
            <td>
              
            </td>
            <td v-for="(list,index) in list.monthLists" :key="index">{{ list.b ? ((list.b1 / list.b) * 100).toFixed(0) : 0 }} %</td>
          </tr>
          <tr>
            <td>
              
            </td>
            <td v-for="(list,index) in list.monthLists" :key="index">{{ list.b ? ((list.b2 / list.b) * 100).toFixed(0) : 0 }} %</td>
          </tr>
          <tr>
            <td>
              
            </td>
            <td v-for="(list,index) in list.monthLists" :key="index">{{ list.b ? ((list.b3 / list.b) * 100).toFixed(0) : 0 }} %</td>
          </tr>
          <tr>
            <td></td>
            <td v-for="(list,index) in list.monthLists" :key="index">{{ list.b ? ((list.b4 / list.b) * 100).toFixed(0) : 0 }} %</td>
          </tr>
        </tbody>
      </v-simple-table>
      </v-alert>

      <!--  -->
      <v-alert outlined>
        <h1 class="text-center">{{ setYear - 1911 }} </h1>
        <VeHistogram :data="chartData3" :settings="chartSet"></VeHistogram>
        <v-simple-table>
        <thead>
          <tr>
            <th class="title font-weight-bold"></th>
            <th class="title font-weight-bold" v-for="month in 12" :key="month.season">{{month}} </th>
          </tr>
        </thead>

        <tbody>
          <tr>
            <td>1</td>
            <td v-for="(list,index) in list.monthLists" :key="index">{{ list.c1 ? ((list.c1 / list.c) * 100).toFixed(0) : 0 }} %</td>
          </tr>
          <tr>
            <td>2</td>
            <td v-for="(list,index) in list.monthLists" :key="index">{{ list.c2 ? ((list.c2 / list.c) * 100).toFixed(0) : 0 }} %</td>
          </tr>
          <tr>
            <td>3</td>
            <td v-for="(list,index) in list.monthLists" :key="index">{{ list.c3 ? ((list.c3 / list.c) * 100).toFixed(0) : 0 }} %</td>
          </tr>
        </tbody>
      </v-simple-table>
      </v-alert>
      <!--  -->
      <v-alert outlined>
        <h1 class="text-center">{{ setYear - 1911 }} </h1>
        <VeHistogram :data="chartData4" :settings="chartSet"></VeHistogram>
        <v-simple-table>
        <thead>
          <tr>
            <th class="title font-weight-bold"></th>
            <th class="title font-weight-bold" v-for="month in 12" :key="month">{{month}} </th>
          </tr>
        </thead>

        <tbody>
          <tr>
            <td></td>
            <td v-for="(list,index) in list.monthLists" :key="index">{{ list.e ? ((list.e / list.d) * 100).toFixed(0) : 0 }} %</td>
          </tr>
        </tbody>
      </v-simple-table>
      </v-alert>
  </div>
</template>

<script>
import VeLine from 'v-charts/lib/line.common'
import VeHistogram from 'v-charts/lib/histogram.common'

export default {
  components:{ VeLine, VeHistogram },
  props:['setYear','list'],

  data(){
    return {
      // 
      chartSetNormal:{
        yAxisType : ['percent'],
      },
      chartSet:{
        yAxisType : ['percent'],
        max:[1]
      },
    }
  },

  computed:{
    //1
    chartData1(){
      return {
        columns: ["", ""],
        rows: this.list.monthLists.map((i,index)=>{
          return {
            '' : (index+1) + '',
            '' : i.b ? (i.b / i.a).toFixed(4) : 0,
          }
        })
      }
    },
    //2
    chartData2(){
      return {
        columns: ["", "", "", "" ,""],
        rows: this.list.monthLists.map((i,index)=>{
          return {
            '' : (index+1) +'',
            '' : i.b ? (i.b1 / i.b) : 0,
            '' :i.b ? (i.b2 / i.b) : 0,
            '' : i.b ? (i.b3 / i.b) : 0,
            '' : i.b ? (i.b4 / i.b) : 0,
          }
        })
      }
    },
    //3
    chartData3(){
      return {
        columns: ["", "1", "2", "3"],
        rows: this.list.monthLists.map((i,index)=>{
          return {
            '' : (index+1) +'',
            '1' : i.c1 ? (i.c1 / i.c) : 0,
            '2' :i.c2 ? (i.c2 / i.c) : 0,
            '3' : i.c3 ? (i.c3 / i.c) : 0,
          }
        })
      }
    },
    //4
    chartData4(){
      return {
        columns: ["", ""],
        rows: this.list.monthLists.map((i,index)=>{
          return {
            '' : (index+1) +'',
            '' : i.e ? (i.e / i.d) : 0,
          }
        })
      }
    },
  }
}
</script>