            <th class="title font-weight-bold"></th>
            <th class="title font-weight-bold"></th>
            <th class="title font-weight-bold" v-for="month in 12" :key="month.season">{{month}} </th>
            <th class="title font-weight-bold"></th>
            <th class="title font-weight-bold"></th>
          </tr>
        </thead>

        <tbody>
          <tr>
            <td>--</td>
            <td>(A)</td>
            <td>--</td>
            <td>--</td>
            <td v-for="(list,index) in list.monthLists" :key="index">{{ list.a }}</td>
            <td> {{ returnTotal(0,'a') }}</td>
            <td>--</td>
          </tr>
          <tr>
            <td>1</td>
            <td>(B):</td>
            <td>B/A</td>
            <td>
              <div class="d-flex align-center">
                <v-text-field hide-details v-model="list.bTval" class="ma-0 pa-0"></v-text-field>%
              </div>
            </td>
            <td v-for="(list,index) in list.monthLists" :key="index">{{ list.b ? ((list.b / list.a) * 100).toFixed(2) : 0 }} %</td>
            <td> {{ returnTotal(2,'b','a') }} %</td>
            <td>{{ returnAverage('b','a') }} %</td>
          </tr>
          <tr>
            <td>2</td>
            <td>(C):</td>
            <td>C/A</td>
            <td>
              <!-- <div class="d-flex align-center">
                <v-text-field hide-details v-model="list.b1Tval" class="ma-0 pa-0"></v-text-field>%
              </div> -->
            </td>
            <td v-for="(list,index) in list.monthLists" :key="index">{{ list.c ? ((list.c / list.a) * 100).toFixed(2) : 0 }} %</td>
            <td> {{ returnTotal(0,'c','a') }} %</td>
            <td>{{ returnAverage('c','a') }} %</td>
          </tr>
          <tr>
            <td>2.1</td>
            <td>(C1):</td>
            <td>C1/A</td>
            <td>
              <!-- <div class="d-flex align-center">
                <v-text-field hide-details v-model="list.b2Tval" class="ma-0 pa-0"></v-text-field>%
              </div> -->
            </td>
            <td v-for="(list,index) in list.monthLists" :key="index">{{ list.c1 ? ((list.c1 / list.a) * 100).toFixed(2) : 0 }} %</td>
            <td> {{ returnTotal(0,'c1','a') }} %</td>
            <td>{{ returnAverage('c1','a') }} %</td>
          </tr>
          <tr>
            <td>3</td>
            <td>(D):</td>
            <td>D/A</td>
            <td>
              <!-- <div class="d-flex align-center">
                <v-text-field hide-details v-model="list.b3Tval" class="ma-0 pa-0"></v-text-field>%
              </div> -->
            </td>
            <td v-for="(list,index) in list.monthLists" :key="index">{{ list.d ? ((list.d / list.a) * 100).toFixed(2) : 0 }} %</td>
            <td> {{ returnTotal(0,'d','a') }} %</td>
            <td>{{ returnAverage('d','a') }} %</td>
          </tr>
          <tr>
            <td>--</td>
            <td>(D1)</td>
            <td>--</td>
            <td>--</td>
            <td v-for="(list,index) in list.monthLists" :key="index">{{ list.d1 }}</td>
            <td> {{ returnTotal(0,'d1') }}</td>
            <td>--</td>
          </tr>
          <tr>
            <td>--</td>
            <td>(D2)</td>
            <td>--</td>
            <td>--</td>
            <td v-for="(list,index) in list.monthLists" :key="index">{{ list.d2 }}</td>
            <td> {{ returnTotal(0,'d2') }}</td>
            <td>--</td>
          </tr>
          <tr>
            <td>3.1</td>
            <td>(D3):</td>
            <td>D3/D1</td>
            <td>
              <!-- <div class="d-flex align-center">
                <v-text-field hide-details v-model="list.b4Tval" class="ma-0 pa-0"></v-text-field>%
              </div> -->
            </td>
            <td v-for="(list,index) in list.monthLists" :key="index">{{ list.d3 ? ((list.d3 / list.d1) * 100).toFixed(2) : 0 }} %</td>
            <td> {{ returnTotal(0,'d3','d1') }} %</td>
            <td>{{ returnAverage('d3','d1') }} %</td>
          </tr>
          <tr>
            <td>3.2</td>
            <td>(D4):</td>
            <td>B4/D2</td>
            <td>
              <!-- <div class="d-flex align-center">
                <v-text-field hide-details v-model="list.b4Tval" class="ma-0 pa-0"></v-text-field>%
              </div> -->
            </td>
            <td v-for="(list,index) in list.monthLists" :key="index">{{ list.d4 ? ((list.d4 / list.d2) * 100).toFixed(2) : 0 }} %</td>
            <td> {{ returnTotal(0,'d4','d2') }} %</td>
            <td>{{ returnAverage('d4','d2') }} %</td>
          </tr>
          <tr>
            <td>4</td>
            <td>(E):</td>
            <td>E/A</td>
            <td>
              <!-- <div class="d-flex align-center">
                <v-text-field hide-details v-model="list.b4Tval" class="ma-0 pa-0"></v-text-field>%
              </div> -->
            </td>
            <td v-for="(list,index) in list.monthLists" :key="index">{{ list.e ? ((list.e / list.a) * 100).toFixed(2) : 0 }} %</td>
            <td> {{ returnTotal(0,'e','a') }} %</td>
            <td>{{ returnAverage('e','a') }} %</td>
          </tr>
          <tr>
            <td>4.1</td>
            <td>(//)(E1):(//)</td>
            <td>E1/A</td>
            <td>
              <!-- <div class="d-flex align-center">
                <v-text-field hide-details v-model="list.b4Tval" class="ma-0 pa-0"></v-text-field>%
              </div> -->
            </td>
            <td v-for="(list,index) in list.monthLists" :key="index">{{ list.e1 ? ((list.e1 / list.a) * 100).toFixed(2) : 0 }} %</td>
            <td> {{ returnTotal(0,'e1','a') }} %</td>
            <td>{{ returnAverage('e1','a') }} %</td>
          </tr>
          <tr>
            <td>4.2</td>
            <td>(E2):</td>
            <td>E2/A</td>
            <td>
              <!-- <div class="d-flex align-center">
                <v-text-field hide-details v-model="list.b4Tval" class="ma-0 pa-0"></v-text-field>%
              </div> -->
            </td>
            <td v-for="(list,index) in list.monthLists" :key="index">{{ list.e2 ? ((list.e2 / list.a) * 100).toFixed(2) : 0 }} %</td>
            <td> {{ returnTotal(0,'e2','a') }} %</td>
            <td>{{ returnAverage('e2','a') }} %</td>
          </tr>
          <tr>
            <td>5</td>
            <td>(F):</td>
            <td>F/A</td>
            <td>
              <!-- <div class="d-flex align-center">
                <v-text-field hide-details v-model="list.b4Tval" class="ma-0 pa-0"></v-text-field>%
              </div> -->
            </td>
            <td v-for="(list,index) in list.monthLists" :key="index">{{ list.f ? ((list.f / list.a) * 100).toFixed(2) : 0 }} %</td>
            <td> {{ returnTotal(0,'f','a') }} %</td>
            <td>{{ returnAverage('f','a') }} %</td>
          </tr>


        </tbody>
      </v-simple-table>
      <v-textarea label="" auto-grow v-model="list.note"></v-textarea>

      <div class="d-flex justify-space-between">
        <v-btn class="error" @click.stop="delData" v-if="workProcess == 'edit'"
          ></v-btn
        >
        <v-spacer></v-spacer>
        <v-btn
          class="primary"
          @click.stop="saveData"
          v-if="workProcess == 'add'"
          :loading="loading"
          :disabled="loading"
          ></v-btn
        >
        <v-btn
          class="success"
          @click.stop="saveData"
          v-if="workProcess == 'edit'"
          :loading="loading"
          :disabled="loading"
          ></v-btn
        >
      </div>

      <v-divider class="my-2"></v-divider>

      <!--  -->
      <Charts :setYear="setYear" :list="list" />
      <!--  end-->
    </div>
  </div>
</template>

<script>
import dayjs from "dayjs";
import PDVFn from "@/assets/js/PDVFn.js";
import * as math from "mathjs"; //mathjs,

import Charts from './yearChart.vue'

export default {
  components:{ Charts },
  props: ["items","matchpipelineItems"], //items
  data() {
    return {
      firstRun: true,  //
      useDataBase : 'analysisofevaluationindicators',
      setYear: dayjs().format("YYYY"),

      list: {
        monthLists:[], //
      }, 
      
      workProcess: "add", //
      loading: false,
      rules: [v => v.length <= 4 || 'Max 4 characters'],
      
      // 
      chartSetNormal:{
        yAxisType : ['percent'],
      },
      chartSet:{
        yAxisType : ['percent'],
        max:[1]
      },
    }
  },
  
  computed: {
    // 
    dataReady() {
      if (
        this.$store.state.userItems.length &&
        this.$store.state.bed_usedItems.length &&
        this.$store.state.behospitalizedItems.length &&
        this.$store.state.analysisofevaluationindicatorsItems
      ) {
        //2022.1.22 ,setYear
        if (this.firstRun){
          this.checkType(); ///
          this.firstRun = false
        }
        return true;
      } else {
        console.log("not yet");
        return false;
      }
    },
  },

  methods:{
    //
    returnTotal(num,key1,key2=null){
      if (key2){
        //key
        let total = 0;
        this.list.monthLists.forEach(list=>{
          let val = list[key1] ? math.format(list[key1]/list[key2]*100) : 0
          total = math.add(total,val)  
        })
        return total.toFixed(num);
      }else{
        //
        return this.list.monthLists.reduce((total,i)=>{
          return total += parseInt(i[key1])
        },0)
      }
    },
    //
    returnAverage(key1,key2){
      let total = 0;
      this.list.monthLists.forEach(list=>{
        let val = list[key1] ? math.format(list[key1]/list[key2]*100) : 0
        total = math.add(total,val)  
      })
      return math.format(total / 12,2);
    },
    ///
    checkType() {
      let match = this.$store.state.analysisofevaluationindicatorsItems
        .map((i) => {
          return {
            snkey: i.snkey,
            ...JSON.parse(i.datalist),
          };
        })
        .find((i) => {
          return i.range == this.setYear && i.rangeType == "year" && i.analysisKind == this.analysisKind;
        });
      if (match) {
        //->
        this.workProcess = "edit";
        this.list = match;
      } else {
        //
        this.workProcess = "add";
        this.loading = true;
        let vm = this;
        setTimeout(function() {
          vm.newData();
        }, 500);
        
      }
    },
    // 
    newData() {
      this.list = {
        monthLists : []
      }
      for (let month = 1; month<=12 ;month++){
        let newMonth = dayjs(`${this.setYear}-${month}`).format("YYYY-MM");
        this.list.monthLists.push(PDVFn.returnMonthDataInfectionEvent(this.items, newMonth, this.matchpipelineItems));  //
      }
      this.loading = false;
    },
    ///
    saveData() {
      this.loading = true;
      if (this.workProcess == "add") {
        //
        //range
        this.list.range = this.setYear;
        this.list.rangeType = "year";
        this.list.create_man =
          this.$store.state.pData.username +
          "(" +
          dayjs().format("YYYY-MM-DD HH:mm:ss") +
          ")";
        var postData = {
          datalist: JSON.stringify(this.list),
        };
        var url = "general/add/";
        this.workProcess = "edit";
      } else {
        //
        if (!this.list.edit_man) {
          this.list.edit_man = "";
        }
        this.list.edit_man =
          this.$store.state.pData.username +
          "(" +
          dayjs().format("YYYY-MM-DD HH:mm:ss") +
          ")" +
          this.list.edit_man;
        var postData = {
          snkey: this.list.snkey,
          datalist: JSON.stringify(this.list),
          edit_man: "pass",
        };
        var url = "general/edit/";
      }

      this.$api.options(
        url + this.$store.state.databaseName + "/" + this.useDataBase,
        postData
      ).then((rs) => {
        if (rs.state == 1) {
          var pop = { msg: "", type: true, theme: "success" };
          this.$store.commit("snackbar", pop);
          this.loading = false;
          this.$store.dispatch("getItems", "analysisofevaluationindicators");  //
        }
      });
    },
    //
    delData() {
      let delData = this.list;
      this.$confirm("?").then((res) => {
        delData.delman =
          this.$store.state.pData.username +
          "(" +
          dayjs().format("YYYY-MM-DD HH:mm:ss") +
          ")";
        var postData = {
          snkey: delData.snkey,
          tablename: this.useDataBase,
          info: JSON.stringify(delData),
        };

        if (res) {
          this.$api.options(
            "general/delv3/" +
              this.$store.state.databaseName +
              "/" +
              postData.tablename,
            postData
          ).then((rs) => {
            if (rs.state == 1) {
              var pop = { msg: "", type: true, theme: "success" };
              this.$store.commit("snackbar", pop);
              this.newData();
              this.workProcess = "add";
              this.$store.dispatch("getItems", "analysisofevaluationindicators");  //
            }
          });
        }
      });
    },
    
  },
}
</script>