<template>
  <div>
    <!--  -->
    <v-alert outlined>
      <h1 class="text-center">
        {{ setYear - 1911 }} 
      </h1>
      <VeHistogram :data="chartData1"></VeHistogram>
    </v-alert>

    <!--  -->
    <v-alert outlined>
      <h1 class="text-center">
        {{ setYear - 1911 }} 
      </h1>
      <VeHistogram :data="chartData2"></VeHistogram>
    </v-alert>

    <!--  -->
    <v-alert outlined>
      <h1 class="text-center">
        {{ setYear - 1911 }} 
      </h1>
      <VeHistogram
        :data="chartData3"
        :settings="chartSetNormal"
      ></VeHistogram>
    </v-alert>

    <!--  -->
    <v-alert outlined>
      <h1 class="text-center">
        {{ setYear - 1911 }} 
      </h1>
      <VeHistogram
        :data="chartData4"
        :settings="chartSetNormal"
      ></VeHistogram>
    </v-alert>
  </div>
</template>

<script>
import VeLine from 'v-charts/lib/line.common'
import VeHistogram from 'v-charts/lib/histogram.common'

export default {
  components:{ VeLine, VeHistogram },
  props:['setYear','list'],

  data(){
    return {
      // 
      chartSetNormal:{
        yAxisType : ['percent'],
      },
      chartSet:{
        yAxisType : ['percent'],
        max:[1]
      },
    }
  },

  computed:{
    //1
    chartData1() {
      return {
        columns: [
          "",
          "",
          "",
          "72",
        ],
        rows: this.list.monthLists.map((i, index) => {
          return {
            : (index+1) + "",
            : i.d,
            : i.e,
            72: i.g,
          };
        }),
      };
    },
    chartData2() {
      return {
        columns: [
          "",
          "",
          "",
          "",
          "",
          "",
        ],
        rows: this.list.monthLists.map((i, index) => {
          return {
            : (index+1) + "",
            : i.i1,
            : i.i2,
            : i.i3,
            : i.i4,
            : i.i5,
          };
        }),
      };
    },
    //3
    chartData3() {
      return {
        columns: ["", ""],
        rows: this.list.monthLists.map((i, index) => {
          return {
            : (index+1) + "",
            : i.b ? i.b / i.a : 0,
          };
        }),
      };
    },
    //4
    chartData4() {
      return {
        columns: ["", "",""],
        rows: this.list.monthLists.map((i, index) => {
          return {
            : (index+1) + "",
            : i.d ? i.d / i.b : 0,
            : i.e ? i.e / i.b : 0,
          };
        }),
      };
    },
  }
}
</script>